
.App {
  max-width: 860px;
  margin: 40px auto;
}
button {
  background: none;
  border: 2px solid #fff;
  padding: 6px 12px;
  border-radius: 4px;
  color: #fff;
  font-weight: bold;
  cursor: pointer;
  font-size: 1em;
}
button:hover {
  background: #0090c1;
  color: #fff;
}
.card-grid {
  position: relative;
  margin-top: 40px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-gap: 20px;
}
.winner {
  box-sizing: content-box;
  position: absolute;
  z-index: 2;
  top:50%;
left:50%;
transform:translate(-50%, -50%);
  border-radius: 6px;
  padding: 40px;
  background-color: #046e8f;
  transition: all 0.2s ease-in;
  border: 4px solid #fff;
}
.winner:hover {
  box-shadow: 8px 2px 16px 5px rgba(0,0,0,0.5);
  box-sizing: content-box;
  
  
}

.loader {
  font: #fff;
  margin: 100px auto;
  position: absolute;
}
.example-enter {
  opacity: 0.01;
}

.example-enter.example-enter-active {
  opacity: 1;
  transition: opacity 500ms ease-in;
}

.example-leave {
  opacity: 0;
}

.example-leave.example-leave-active {
  opacity: 0.01;
  
}


@media (max-width: 900px) {
  .App {
    width: 80%;
  }
  h1 {
    font-size: 0.7em;
    
  }
  .winner {
    top:50%;
left:50%;
transform:translate(-50%, -50%);
    font-size: 1.5em;
    border: 1px solid #fff;
  }
  .card-grid {
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 10px;
  }
}