.card {
    position: relative;
}
.card img{
    width: 100%;
    display: block;
    border: 4px solid #fff;
    /* border-image: linear-gradient(to right bottom, #3c350b, #eea507); */
    border-radius: 6px; 
    border-image-slice: 1; 
    max-height: 200px; 
}

.win img {
    border: 4px  solid green;
    border-image: none;
}
.card .front {
    background: rgb(162,126,142);
background: radial-gradient(circle, #183446 0%, rgba(0, 0, 0, 0.922) 100%);
 transform: rotateY(90deg);
 position: absolute;
 transition: transform 0.2s ease-in;
}
.flipped .front {
 transform: rotateY(0deg);
 transition-delay: 0.2s;
}

.card .back {
    
    
    transition: all ease-in 0.2s;
    transition-delay: 0.2s;
}
.flipped .back {
    transform: rotateY(90deg);
    transition-delay: 0s;
}

@media (max-width: 900px) {
    .card img {
        border: 1px solid #fff;
    }
    .win img {
        border: 1px  solid green;
        border-image: none;
    }
}